import { t } from 'i18next';

import { baseService } from '../app/baseService';
import { HttpMethods } from '../lookups/httpMethods';
import IBooking from '../interfaces/IQuest';
import IQuest from '../interfaces/IQuest';


const URL = '/quests';

export interface IGetQuestsPayload {
    search: string;
    page: number;
    rpp: number;
}

interface ICreateQuest {
    name: string;
    category: string;
    published: boolean;
    premium: boolean;
    stations: number[];
    image: string;
}

interface IUpdateQuest {
    id: string;
    name: string;
    category: string;
    published: boolean;
    premium: boolean;
    stations: number[];
    image: string;
}

export const questService = baseService.injectEndpoints({
    endpoints: (builder) => ({
        getQuests: builder.query<IQuest[], IGetQuestsPayload>({
            query: (data) => ({
                url: `${URL}/?search=${data.search}`,
                method: HttpMethods.GET,
            }),
            transformResponse: (response: IQuest[]) => {
                const quests: IQuest[] = response.map((x) => {
                    return {
                        id: x.id,
                        name: x.name,
                        category: x.category,
                        published: x.published,
                        premium: x.premium,
                        stations: x.stations,
                        image: x.image,
                    };
                });
                return quests;
            },
        }),
        getQuestById: builder.query<IQuest, string>({
            query: (questId) => ({
                url: `${URL}/${questId}`,
            }),
        }),
        createQuest: builder.mutation<void, ICreateQuest>({
            query: (data) => ({
                url: `${URL}/create`,
                method: HttpMethods.POST,
                body: data,
            }),
            //invalidatesTags: [''],
        }),
        updateQuest: builder.mutation<void, IUpdateQuest>({
            query: (data) => ({
                url: `${URL}/${data.id}`,
                method: HttpMethods.PUT,
                body: data,
            }),
            //invalidatesTags: [''],
        }),
        deleteQuest: builder.mutation<void, string>({
            query: (questId) => ({
                url: `${URL}/${questId}`,
                method: HttpMethods.DELETE,
            }),
            //invalidatesTags: [''],
        }),
    }),
});

export const {
    useLazyGetQuestsQuery,
    useLazyGetQuestByIdQuery,
    useCreateQuestMutation,
    useUpdateQuestMutation,
    useDeleteQuestMutation,
} = questService;
