import { useTranslation } from "react-i18next";
import { cloneDeep, groupBy } from "lodash";
import { useEffect, useState } from "react";
import { Link, useSearchParams } from "react-router-dom";

import MainWrapper from "../components/MainWrapper";
import LoaderAvailableLessons from "../components/skeleton-loaders/LoaderAvailableLessons";
import { useAppSelector } from "../../app/hooks";
import { PATHS } from "../../app/routes";
import ICompletedLesson from "../../interfaces/IQuest";
import {
  useLazyGetQuestsQuery,
  IGetQuestsPayload,
} from "../../services/questService";
import CompletedLessonsItem from "./components/CompletedLessonsItem";
import QuestForm from "./components/QuestForm";
import IQuest from "../../interfaces/IQuest";
import noResults from "../../assets/images/noResults.svg";

const CompletedLessons = () => {
  const { t } = useTranslation();

  const [
    getQuests,
    { isLoading: listLoading, isUninitialized: listUninitialized },
  ] = useLazyGetQuestsQuery();

  const [getQuestsPayload, setGetQuestsPayload] = useState<IGetQuestsPayload>({
    search: "",
    page: 0,
    rpp: 10,
  });

  const [activeQuest, setActiveQuest] = useState<IQuest | null>(null);
  const [completedLessonsState, setCompletedLessonsState] = useState<
    ICompletedLesson[]
  >([]);

  const loadingList = listLoading || listUninitialized;

  const handleActiveQuests = async (lessonId: string) => {
    if (completedLessonsState) {
      const currentlyActiveQuest = completedLessonsState.find(
        (currentLessonId: ICompletedLesson) => currentLessonId.id === lessonId
      );
      setActiveQuest(currentlyActiveQuest ? currentlyActiveQuest : null);

      // handle
    }
  };

  const fetchData = async () => {
    const completedLessonsResponse = await getQuests(getQuestsPayload).unwrap();
    setCompletedLessonsState(completedLessonsResponse);
    setActiveQuest(completedLessonsResponse[0]);
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <>
      <MainWrapper>
        <div className="card--lessons">
          <div className="card--lessons__body">
            <div className="card--lessons__body__aside">
              <div className="card--lessons__head">
                <div>{t("QUESTS.TITLE")}</div>
              </div>
              <div className="mt-10 mb-10 ml-6 mr-6">
                <span className="type--uppercase type--color--tertiary">
                  {t("QUESTS.QUESTS_AVAILABLE")}
                </span>
                <span className="tag--primary d--ib ml-2">
                  {completedLessonsState.length
                    ? completedLessonsState.length
                    : "0"}
                </span>
              </div>
              <div className="lessons-list">
                {loadingList ? (
                  <LoaderAvailableLessons />
                ) : completedLessonsState.length > 0 ? (
                  completedLessonsState.map((lesson: ICompletedLesson) => {
                    return (
                      <CompletedLessonsItem
                        key={lesson.id}
                        quest={lesson}
                        activeQuest={activeQuest ? activeQuest.id : ""}
                        handleActiveQuests={handleActiveQuests}
                      />
                    );
                  })
                ) : (
                  <>
                    <div className={`lessons-list__item mt-6`}>
                      <div className="lessons-list__item__info">
                        <div className="type--wgt--bold">
                          {t("QUESTS.EMPTY_QUESTS_TITLE")}
                        </div>
                        <div className="type--color--brand">
                          {t("QUESTS.EMPTY_QUESTS_LIST")}
                        </div>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </div>
            <div className="card--lessons__body__main flex">
              <div className="flex--primary w--100">
                <QuestForm quest={activeQuest} />
              </div>
            </div>
            <div className="card--lessons__body__main flex">
              {activeQuest ? (
                <div className="flex--primary  w--50">
                  <div className="flex flex--center">
                    <img
                      className="image__profile image__profile--md mr-4"
                      src={"https://" + activeQuest.image}
                      alt="quest"
                    />
                    <div>
                      <div className="type--md mb-1">{activeQuest.name}</div>
                      <div className="type--color--brand">
                        {activeQuest.premium}
                      </div>
                    </div>
                  </div>
                  <div>
                    <button
                      onClick={() => console.log("click")}
                      className="btn btn--base btn--clear mr-4"
                    >
                      {t("QUESTS.LEAVE_REVIEW")}
                    </button>

                    <Link className="btn btn--primary btn--base" to="">
                      {t("QUESTS.VIEW_CALENDAR")}
                    </Link>
                  </div>
                </div>
              ) : (
                <>
                  <div className="flex--primary flex--jc--center flex--col w--100">
                    <img src={noResults} />
                    <div className="type type--color--tertiary">
                      {t("QUESTS.CHOOSE_QUEST")}
                    </div>
                  </div>
                </>
              )}
            </div>
          </div>
        </div>
      </MainWrapper>
    </>
  );
};

export default CompletedLessons;
