import { useTranslation } from "react-i18next";
import { Form, Formik, FormikProvider, useFormik } from "formik";
import * as Yup from "yup";
import IQuest from "../../../interfaces/IQuest";
import MySelect from "../../components/form/MySelectField";
import MyTextArea from "../../components/form/MyTextArea";
import TextField from "../../components/form/TextField";
import UploadFile from "../../components/form/MyUploadField";

const QuestForm: React.FC<any> = (quest: IQuest | null) => {
  const { t } = useTranslation();

  const categoryOptions = [
    { label: "Gastro", value: "1" },
    { label: "Nature", value: "2" },
    { label: "Adrenaline", value: "3" },
  ];

  const initialValues: IQuest = {
    id: "",
    name: "",
    category: "",
    published: true,
    premium: false,
    stations: [],
    image: "",
  };

  const formik = useFormik({
    initialValues: initialValues,
    onSubmit: (values: IQuest) => {
      const data = {
        id: values.id,
        name: values.name,
        category: values.category,
        published: values.published,
        premium: values.premium,
        stations: values.stations,
      };
    },
    validationSchema: Yup.object().shape({
      email: Yup.string().required(t("FORM_VALIDATION.REQUIRED")),
      password: Yup.string()
        .min(2, t("FORM_VALIDATION.TOO_SHORT"))
        .max(50, t("FORM_VALIDATION.TOO_LONG"))
        .required(t("FORM_VALIDATION.REQUIRED")),
    }),
  });

  return (
    <div className="layout--primary w--100">
      <div>
        <FormikProvider value={formik}>
          <Form>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.NAME")}
              </label>
              <TextField
                name="name"
                id="name"
                placeholder={t("QUESTS.FORM.NAME_PLACEHOLDER")}
                className="input input--base input--text input--icon"
              />
            </div>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.DESCRIPTION")}
              </label>
              <MyTextArea
                name="description"
                id="description"
                placeholder={t("QUESTS.FORM.DESCRIPTION_PLACEHOLDER")}
                maxLength={500}
              />
            </div>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.IMAGE")}
              </label>
            </div>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.CATEGORY")}
              </label>
              <MySelect
                field={formik.getFieldProps("level")}
                form={formik}
                meta={formik.getFieldMeta("level")}
                classNamePrefix="onboarding-select"
                isMulti={false}
                options={categoryOptions ? categoryOptions : []}
                placeholder={t("QUESTS.FORM.CATEGORY_PLACEHOLDER")}
              />
            </div>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.STATIONS")}
              </label>
              <MySelect
                field={formik.getFieldProps("level")}
                form={formik}
                meta={formik.getFieldMeta("level")}
                classNamePrefix="onboarding-select"
                isMulti={true}
                options={categoryOptions ? categoryOptions : []}
                placeholder={t("QUESTS.FORM.STATIONS_PLACEHOLDER")}
              />
            </div>
            <div className="field">
              <label className="field__label" htmlFor="password">
                {t("QUESTS.FORM.REWARD")}
              </label>
              <MySelect
                field={formik.getFieldProps("level")}
                form={formik}
                meta={formik.getFieldMeta("level")}
                classNamePrefix="onboarding-select"
                isMulti={false}
                options={categoryOptions ? categoryOptions : []}
                placeholder={t("QUESTS.FORM.REWARD_PLACEHOLDER")}
              />
            </div>
            <div>
              <button
                onClick={() => console.log("click")}
                className="btn btn--base btn--primary mr-4"
              >
                {t("QUESTS.FORM.CREATE")}
              </button>
            </div>
          </Form>
        </FormikProvider>
      </div>
    </div>
  );
};

export default QuestForm;
