import { t } from 'i18next';

import ICompletedLesson from '../../../interfaces/IQuest';

interface Props {
    quest: ICompletedLesson;
    activeQuest: string;
    handleActiveQuests: (lessonId: string) => void;
}

const CompletedLessonsItem = (props: Props) => {
    const { quest, activeQuest, handleActiveQuests } = props;

    return (
        <div
            key={quest.id}
            className={`lessons-list__item ${activeQuest === quest.id ? 'active' : ''}`}
            onClick={() => handleActiveQuests(quest.id)}
        >
            <img className="lessons-list__item__img" src={'https://' + quest.image} alt="tutor profile picture" />
            <div className="lessons-list__item__info">
                <div className="type--wgt--bold">
                    {quest.name}
                </div>
                <div className="type--color--brand">{quest.category}</div>
            </div>
            <div>
                {quest.premium}
            </div>
        </div>
    );
};

export default CompletedLessonsItem;
